import { createElement, ensureElement } from '../utils/utils';
import { Component } from './base/Component';
export class Basket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._catalog = ensureElement(`.basket__list`, this.container);
        this._price = ensureElement(`.basket__price`, this.container);
        this._button = container.querySelector(`.basket__button`);
        this.items = [];
        if (this._button) {
            this._button.addEventListener('click', () => {
                this.events.emit('order:open');
            });
        }
    }
    updateButtonState() {
        const totalPrice = parseFloat(this._price.textContent || '0');
        if (totalPrice > 0) {
            this.setDisabled(this._button, false);
        }
        else {
            this.setDisabled(this._button, true);
        }
    }
    set price(value) {
        this._price.textContent = `${value} синапсов`;
        this.updateButtonState();
    }
    set items(items) {
        if (items.length) {
            this._catalog.replaceChildren(...items);
        }
        else {
            this._catalog.replaceChildren(createElement('p', { textContent: 'Корзина пуста' }));
        }
        this.updateButtonState();
    }
}
