export class OrderData {
    constructor(events) {
        this.events = events;
        this._order = {
            total: 0,
            items: [],
            email: '',
            phone: '',
            address: '',
            payment: '',
        };
        this.events = events;
    }
    get formErrors() {
        return this._formErrors;
    }
    get order() {
        return this._order;
    }
    setOrderPayment(value) {
        this._order.payment = value;
    }
    setOrderEmail(value) {
        this._order.email = value;
    }
    setOrderAddress(value) {
        this._order.phone = value;
    }
    setOrderField(field, value) {
        this._order[field] = value;
        this.validateOrder();
    }
    validateOrder() {
        const errors = {};
        if (!this._order.payment) {
            errors.payment = 'Укажите способ оплаты';
        }
        if (!this._order.email) {
            errors.email = 'Укажите ваш e-mail';
        }
        if (!this._order.address) {
            errors.address = 'Укажите адрес доставки';
        }
        if (!this._order.phone) {
            errors.phone = 'Укажите номер телефона';
        }
        this._formErrors = errors;
        this.events.emit('errors:change', this._formErrors);
        return Object.keys(errors).length === 0;
    }
    clearOrder() {
        this._order = {
            total: 0,
            items: [],
            email: '',
            phone: '',
            address: '',
            payment: '',
        };
    }
}
