export class AppApi {
    constructor(cdn, baseApi) {
        this._baseApi = baseApi;
        this.cdn = cdn;
    }
    getProducts() {
        return this._baseApi
            .get(`/product`)
            .then((response) => response.items.map((product) => (Object.assign(Object.assign({}, product), { image: this.cdn + product.image }))));
    }
    getProduct(id) {
        return this._baseApi
            .get(`/product/${id}`)
            .then((product) => (Object.assign(Object.assign({}, product), { image: this.cdn + product.image })));
    }
    orderProducts(order) {
        return this._baseApi
            .post('/order', order)
            .then((data) => data);
    }
}
